/* harmony default export */ var register = ({
  components: {
    navTop: src_components_nav
  },
  data: function data() {
    return {
      week: [],
      fullDate: [],
      date: [],
      curChoose: 0, //选择的日期
      curTime: '', //选择早班晚班
      timer: new Date(),
      morningIsDisabled: false,
      afternoonIsDisabled: false,
      timeout: ''
    };
  },
  created: function created() {
    var aWeek = new Array("日", "一", "二", "三", "四", "五", "六");
    //获取未来七天
    for (var i = 0; i < 7; i++) {
      var date = new Date();
      date.setDate(new Date().getDate() + i);
      this.fullDate.push(date.Format('yyyy-MM-dd'));
      this.date.push(date.getDate());
      this.week.push(aWeek[date.getDay()]);
    }
    this.count();
  },

  methods: {
    count: function count() {
      var _this = this;

      var serverTime = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : new Date();

      this.checkTime();
      this.timeout = setTimeout(function () {
        _this.timer = new Date(serverTime.getTime() + 1000);
        console.log(_this.timer);
        _this.count(_this.timer);
      }, 1000);
    },
    checkTime: function checkTime() {
      var now = this.timer;
      var hours = now.getHours();
      var minute = now.getMinutes();
      //上午
      //有剩余号源 且 (选中日期为当天 且 时间在9点前) || (选中日期不为当天 且 时间在7点~22点) 
      if (1 == 1 && this.date[this.curChoose] == now.getDate() && hours < 9 || this.date[this.curChoose] != now.getDate() && hours > 7 && hours < 22) {
        this.morningIsDisabled = false;
      } else {
        this.morningIsDisabled = true;
      }
      //下午
      //有剩余号源 且 (选中日期为当天 且 时间在17:30前) || (选中日期不为当天 且 时间在7点~22点)
      if (1 == 1 && this.date[this.curChoose] == now.getDate() && (hours < 17 || hours == 17 && minute < 30) || this.date[this.curChoose] != now.getDate() && hours > 7 && hours < 22) {
        this.afternoonIsDisabled = false;
      } else {
        this.afternoonIsDisabled = true;
      }
    },
    chooseDay: function chooseDay(i, day) {
      this.curChoose = i;
      //重置选择上午下午
      this.curTime = '';
      console.log(day);
      this.checkTime();
    },
    comfirm: function comfirm() {
      if (this.curTime) {
        this.$router.push({
          path: '/register_form',
          query: {
            date: this.fullDate[this.curChoose],
            week: this.week[this.curChoose],
            time: this.curTime
          }

        });
      } else {
        return false;
      }
    },
    showTips: function showTips(when) {
      if (when == 'morning' && this.morningIsDisabled || when == 'afternoon' && this.afternoonIsDisabled) {
        message_box_default.a.alert('该时间段已无法预约');
      }
    }
  },
  beforeRouteLeave: function beforeRouteLeave(to, from, next) {
    console.log(to, from);
    clearTimeout(this.timeout);
    next();
  }
});
