  components: {
    navTop: src_components_nav,
    mtButton: button_default.a,
    mtField: field_default.a
  },
  data: function data() {
    return {
      appointment: [{
        name: '',
        phone: '',
        address: '',
        sex: 'man'
      }],
      popupVisible: false,
      currentAppointment: 0,
      info: [{
        name: '小陈（男)',
        phone: '13555555555',
        address: '澄海区XXXXXXXXXXXXXXXX澄海区XXXXXXXXXXXXXXXX',
        sex: 'woman'
      }, {
        name: '小陈（男)',
        phone: '13555555555',
        address: '澄海区XXXXXXXXXXXXXXXX澄海区XXXXXXXXXXXXXXXX',
        sex: 'woman'
      }, {
        name: '小陈（男)',
        phone: '13555555555',
        address: '澄海区XXXXXXXXXXXXXXXX澄海区XXXXXXXXXXXXXXXX',
        sex: 'woman'
      }]
    };
  },
  mounted: function mounted() {},

  methods: {
    //删除预约人
    deleteAppointment: function deleteAppointment(i) {
      var _this = this;

      console.log(i);
      message_box_default.a.confirm('确定删除该预约人信息吗?').then(function (action) {
        _this.appointment.splice(i, 1);
      }, function () {
        console.log('cancel');
      });
    },

    //添加预约人
    add_appointment: function add_appointment() {
      var appointment = {
        name: '',
        phone: '',
        address: '',
        sex: 'man'
      };
      this.appointment.push(appointment);
      this.$nextTick(function () {
        this.$refs.register_form.scrollTop = this.$refs.register_form.scrollHeight;
      });
    },

    //显示常用预约人弹层
    showCommonAppointment: function showCommonAppointment(i) {
      this.popupVisible = true;
      this.currentAppointment = i;
    },

    //选择常用预约人
    uesCommonAppointment: function uesCommonAppointment(i) {
      this.$set(this.appointment, this.currentAppointment, this.info[i]);
      this.popupVisible = false;
    },

    //提交前验证
    comfirmValidate: function comfirmValidate() {
      var len = this.appointment.length;
      if (len == 0) {
        message_box_default.a.alert('请添加预约人');
        return false;
      } else {
        for (var i = 0; i < this.appointment.length; i++) {
          for (var key in this.appointment[i]) {
            if (this.appointment[i][key] == '') {
              message_box_default.a.alert('请完整填写预约人信息后再提交');
              return false;
            }
          }
        }
        //判断名额是否充足
        this.enoughPopup();
      }
    },
    enoughPopup: function enoughPopup() {
      //调起微信支付
      this.$router.push('/register_result');
    },
    notEnoughPopup: function notEnoughPopup() {
      message_box_default()({
        title: '提示',
        message: '当前剩余名额不足(余2人)<br>无法进行预约'
        //showCancelButton: true
      });
    }
  }
